import { React, useState, useEffect } from 'react'
import OrderTable from './OrderTable'
import { useSelector, useDispatch } from "react-redux";
import { changeIsNeedInvoice, setUserID, setPhone } from '../../redux/user/userActions'
function OrderInfo() {
    const dispatch = useDispatch();

    const style = {
        container: {
            width: '100%',
            maxWidth: '768px',
            margin: '0 auto',
            justifyContent: 'center',
            alignItems: 'center',
            display: 'block'
        },
        title: {
            textAlign: 'center'
        },
        list: {
            padding: '10px 0',
            borderBottom: '1px solid #f3f3f3'
        },
        formTitle: {
            display: 'block',
            textAlign: 'left',
            margin: '5px auto',
            fontSize: '14px'
        },
        formList: {
            padding: '5px 0'
        },
        formInput: {
            width: '90%',
            padding: '10px 0',
            margin: '5px auto',
            lineHeight: '20px',
            background: '#F3F3F3',
            WebkitBorderRadius: '5px',
            border: 'none'
        },
        moblieInput: {
            justifyContent: 'space-between',
            marginBottom: '10px',
            display: 'flex',
            alignItems: 'center',
            width: '90%'
        },
        mobileNumber: {
            width: '90%',
            padding: '10px',
            lineHeight: '20px',
            background: '#F3F3F3',
            borderRadius: '5px 0 0 5px'
        },
        modifyButton: {
            padding: '10px',
            color: '#FFFFFF',
            lineHeight: '20px',
            whiteSpace: 'nowrap',
            background: '#047BFE',
            borderRadius: '0 5px 5px 0'
        },
        textArea: {
            display: 'block',
            width: '90%',
            minHeight: '90px',
            background: '#F3F3F3',
            WebkitBorderRadius: '5px'
        },
        checkbox: {
            display: 'flex',
            flexFlow: 'nowrap',
            justifyContent: 'flex-start',
            alignItems: 'center',
            padding: '10px 0'
        },
        checkboxLabel: {
            display: 'flex',
            alignItems: 'center',
            height: '20px'
        }
    }
    const userStore = useSelector(state => state.userStore)

    const [comment, setComment] = useState('')


    function InfoTable() {
        return (
            <div>
                <ul style={{ listStyle: 'none' }}>
                    <li style={style.list}> {userStore.orderWay.type}</li>
                    {userStore.orderWay.type === '' && <li style={style.list}> {userStore.orderWay.value}</li>}
                    <li style={style.list}> {userStore.takeTime}</li>
                    <li style={style.list}> </li>
                    <li style={style.list}> 12345678</li>
                </ul>
            </div>
        )
    }

    function handleIsNeedInvoice() {
        dispatch(changeIsNeedInvoice())
    }


    function SecondTitle() {
        return (
            <h3 style={style.title}></h3>
        )
    }

    return (
        <div style={style.container}>
            <InfoTable />
            <OrderTable display={'block'} />
            <SecondTitle />
            {/* <OrderInfoForm /> */}
            <div style={{ marginBottom: '100px' }}>
                <ul style={{ listStyle: 'none' }}>
                    <li>
                        <label style={style.formTitle}><span style={{ color: '#CB5A4B' }}>*</span></label>
                        <div>
                            <input style={style.formInput} type='text' placeholder='' value={userStore.userID} onChange={(e) => dispatch(setUserID(e.target.value))} />
                        </div>
                    </li>
                    <li>
                        <label style={style.formTitle}><span style={{ color: '#CB5A4B' }}>*</span></label>
                        <div>
                            <input style={style.formInput} type='tel' placeholder='' value={userStore.phone} onChange={(e) => dispatch(setPhone(e.target.value))} />
                        </div>
                    </li>
                    <li>
                        <label style={style.formTitle}>( 200 )</label>
                        <textarea style={style.textArea} value={comment} onChange={(e) => setComment(e.target.value)} />
                    </li>
                    <li>
                        <div style={style.checkbox}>
                            <input type='checkbox' id='invoice1' checked={userStore.needInvoice} onChange={handleIsNeedInvoice} />
                            <label htmlFor='invoice1' style={style.checkboxLabel}>
                                <p></p>
                            </label>
                        </div>
                    </li>
                </ul>
            </div>
        </div>
    )
}

export default OrderInfo
